/* Общие стили для всех страниц */
.page-container {
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.page-header {
  margin-bottom: 16px; /* Унифицированный отступ после заголовка */
  flex-shrink: 0; /* Предотвращаем сжатие заголовка */
}

.page-header h1 {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 8px;
}

.page-description {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0;
}

.page-content {
  background-color: var(--secondary-bg);
  border-radius: 8px;
  padding: 24px;
  border: 1px solid var(--border-color);
}

.account-info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.info-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.info-group label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.info-value {
  color: var(--text-primary);
  font-size: 1rem;
  padding: 12px;
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px solid var(--border-color);
}

/* Общие стили для всех карточек/секций */
.productivity-card,
.subscription-card,
.settings-card,
.report-card,
.statistics-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

/* Заголовок секции */
.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* Grid layouts с фиксированной высотой и скроллом */
.dashboard-grid,
.reports-grid,
.productivity-sections,
.settings-sections,
.subscription-grid,
.statistics-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  height: 70vh; /* Уменьшаем высоту грида */
  overflow-y: auto;
  min-height: 0;
}

/* Убираем overflow из всех внутренних элементов */
.sites-table,
.trends-list,
.categories-list,
.recommendations-list,
.payments-list,
.plans-grid {
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px solid var(--border-color);
}

/* Стили для списков с элементами */
.trend-item,
.category-item,
.recommendation-item,
.payment-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid var(--border-color);
}

.trend-item:last-child,
.category-item:last-child,
.recommendation-item:last-child,
.payment-item:last-child {
  border-bottom: none;
}

.trend-category {
  color: var(--text-primary);
}

.trend-change {
  font-weight: 500;
}

.trend-change.increasing {
  color: #10B981;
}

.trend-change.decreasing {
  color: #EF4444;
}

.trend-change.stable {
  color: #F59E0B;
}

/* Специальные стили для subscription-grid */
.subscription-grid {
  grid-template-areas:
    "current plans"
    "billing history";
}

.subscription-grid > *:nth-child(1) { grid-area: current; }
.subscription-grid > *:nth-child(2) { grid-area: plans; }
.subscription-grid > *:nth-child(3) { grid-area: billing; }
.subscription-grid > *:nth-child(4) { grid-area: history; }

/* Стили для плейсхолдеров графиков и диаграмм */
.report-chart-placeholder,
.productivity-chart-placeholder,
.trends-chart-placeholder {
  height: 200px; /* Уменьшаем высоту графиков */
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px dashed var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  margin: 16px 0;
}

/* Responsive styles */
@media (max-width: 1024px) {
  .dashboard-grid,
  .reports-grid,
  .productivity-sections,
  .settings-sections,
  .subscription-grid {
    grid-template-columns: 1fr;
    grid-template-areas:
      "current"
      "plans"
      "billing"
      "history";
  }
}

@media (max-width: 768px) {
  .page-container {
    padding: 16px;
  }

  .page-header h1 {
    font-size: 1.5rem;
  }
}

/* Стили для контейнера фильтров в Statistics */
.stats-controls {
  margin-bottom: 16px; /* Такой же отступ как у page-header */
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.stats-filters {
  display: flex;
  gap: 16px;
  align-items: center;
}

.filter-group {
  display: flex;
  gap: 8px;
  align-items: center;
}

.date-picker {
  display: flex;
  gap: 8px;
}

/* Обновляем стили для таблицы */
.sites-table {
  margin-top: 16px;
  background: var(--secondary-bg);
  border-radius: 8px;
  border: 1px solid var(--border-color);
  height: 300px; /* Фиксированная высота для таблицы */
} 